export default function VenueHighlights() {
  return (
    <section className="py-16 px-4 bg-[#23234a] text-cream" data-aos="fade-in">
      <h2
        className="text-3xl md:text-5xl font-serif font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 via-yellow-300 to-yellow-600 drop-shadow-lg mb-4 text-center"
        style={{ letterSpacing: '2px' }}
        data-aos="zoom-in"
      >
        Venue Highlights
      </h2>
      <div className="flex justify-center mb-8">
        <span className="block w-24 h-1 rounded-full bg-gradient-to-r from-yellow-400 via-yellow-300 to-yellow-600"></span>
      </div>
      <div className="flex justify-center items-center mb-10">
        <img src="/front2.jpg" alt="Front View" className="rounded-xl shadow-lg w-full md:w-2/3 h-80 object-contain" data-aos="fade-up" />
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8 max-w-6xl mx-auto">
        {highlights.map((feature, idx) => (
          <div key={idx} className="bg-[#1a1a2e] rounded-xl shadow-lg p-8 flex flex-col items-center text-center hover:scale-105 transition-transform duration-300" data-aos="fade-up" data-aos-delay={idx * 100}>
            <img src={feature.img} alt={feature.title + ' image'} className="mb-4 w-full h-40 object-cover rounded-lg" />
            <span className="text-5xl mb-4">{feature.icon}</span>
            <h3 className="text-xl font-semibold text-gold mb-2">{feature.title}</h3>
            <p className="text-cream text-base">{feature.description}</p>
          </div>
        ))}
      </div>
    </section>
  );
}
